







































































































































































import { Vue, Component, Watch } from 'vue-property-decorator';
import { DesignData } from '@/types/views/design.interface';
import BaseImg from '@/components/BaseImg.vue';
import BaseAvatar from '@/components/BaseAvatar.vue';

@Component({
  components: {
    BaseImg,
    BaseAvatar
  },

  data(): DesignData {
    return {
      tabItems: [], // 选项卡
      activeTab: 0, // 当前选中的选项卡
      activeTabBtn: 0, // 当前选中的选项卡下的二级按钮
      currentTabData: [], // 当前要展示的数据
      currentTabPaginationData: {}, // 当前要展示的数据的分页信息
      tabLoading: false // 选项卡内容加载中
    };
  }
})
export default class Design extends Vue {
  tabItems!: any[]; // 选项卡
  activeTab!: number; // 当前选中的选项卡
  activeTabBtn!: number; // 当前选中的选项卡下的二级按钮
  currentTabData!: object[]; // 当前要展示的数据
  currentTabPaginationData!: object; // 当前要展示的数据的分页信息
  tabLoading!: boolean; // 选项卡内容加载中

  // 监控 activeTab 的变化，如果没变化的话，就不要重新请求数据
  @Watch('activeTab')
  onActiveTabChanged(val: number, oldVal: number) {
    if (val !== oldVal) {
      this.getCurrentTabData(val, this.tabItems[val].activeButton);
    }
  }

  // 监控 activeTabBtn 的变化，如果没变化的话，就不要重新请求数据
  @Watch('activeTabBtn')
  onActiveTabBtnChanged(val: number, oldVal: number) {
    if (val !== oldVal) {
      this.getCurrentTabData(this.activeTab, this.tabItems[this.activeTab].activeButton);
    }
  }

  // 路由跳转相关
  goToPage(to: any) {
    if (to.name) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.name === to.name) {
        return;
      }
      this.$router.push({
        name: to.name
      });
    } else if (to.path) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.path === to.path) {
        return;
      }
      this.$router.push({
        path: to.path
      });
    }
  }

  // 获取当前要展示的数据
  getCurrentTabData(i: number, j: number) {
    // console.log(i, j);

    // const items = this.tabItems[i].items;
    // // console.log(items);
    // const indexs = this.tabItems[i].buttons[j].items;
    // // console.log(indexs);
    // if (items && indexs) {
    //   this.currentTabData = items.filter((element: any) => {
    //     return indexs.includes(Number(element.id));
    //   });
    //   this.currentTabPaginationData = this.tabItems[i].buttons[j].pagination;
    //   // console.log(this.currentTabData);
    // } else {
    //   this.currentTabData = [];
    // }

    this.tabLoading = true;
    setTimeout(() => {
      this.tabLoading = false;

      const items = this.tabItems[i].items;
      // console.log(items);
      const indexs = this.tabItems[i].buttons[j].items;
      // console.log(indexs);
      if (items && indexs) {
        this.currentTabData = items.filter((element: any) => {
          return indexs.includes(Number(element.id));
        });
        this.currentTabPaginationData = this.tabItems[i].buttons[j].pagination;
        // console.log(this.currentTabData);
      } else {
        this.currentTabData = [];
      }
    }, 400);
  }

  // 获取选项卡后台数据
  fetchTabItemsData() {
    // this.$api.userApi.design.getTabItems().then(
    //   (response: any) => {
    //     // console.log(response.data);
    //     this.tabItems = response.data;
    //   },
    //   (error: any) => {
    //     console.log(`${error.response.status}: ${error.response.statusText}`);
    //   }
    // );

    this.tabItems = this.$api.userApi.design.getTabItems().data;
    // this.tabLoading = true;
    // setTimeout(() => {
    //   this.tabLoading = false;
    //   this.tabItems = this.$api.userApi.design.getTabItems().data;
    // }, 400);
  }

  // 生命周期钩子函数
  created() {
    // this.fetchTabItemsData();
    this.tabItems = this.$api.userApi.design.getTabItems().data;
    this.getCurrentTabData(0, 0);
  }
}
